    body {
      line-height: 1.0;
      min-height: var(--viewport-height);
      min-width: 320px;
      overflow-x: hidden;
      word-wrap: break-word;
    }

    body:before {
      background-attachment: scroll;
      content: '';
      display: block;
      height: var(--background-height);
      left: 0;
      pointer-events: none;
      position: fixed;
      top: 0;
      transform: scale(1);
      width: 100vw;
      z-index: 0;
      background-image: linear-gradient(45deg, #DEE6FA 0%, #E6EBFA 60%, #D0D5F2 100%);
      background-position: 0% 0%;
      background-repeat: repeat;
      background-size: auto;
    }

    :root {
      --background-height: 100vh;
      --site-language-alignment: left;
      --site-language-direction: ltr;
      --site-language-flex-alignment: flex-start;
      --site-language-indent-left: 1;
      --site-language-indent-right: 0;
      --site-language-margin-left: 0;
      --site-language-margin-right: auto;
      --viewport-height: 100vh;
    }

    html {
      font-size: 18pt;
    }

    a {
      color: inherit;
      text-decoration: underline;
      transition: color 0.25s ease;
    }

    a[onclick]:not([href]) {
      cursor: pointer;
    }

    #wrapper {
      -webkit-overflow-scrolling: touch;
      align-items: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: var(--viewport-height);
      overflow: hidden;
      position: relative;
      z-index: 2;
    }

    #main {
      --alignment: center;
      --flex-alignment: center;
      --indent-left: 1;
      --indent-right: 1;
      --margin-left: auto;
      --margin-right: auto;
      --border-radius-tl: 0;
      --border-radius-tr: 0;
      --border-radius-br: 0;
      --border-radius-bl: 0;
      align-items: center;
      display: flex;
      flex-grow: 0;
      flex-shrink: 0;
      justify-content: center;
      max-width: 100%;
      position: relative;
      text-align: var(--alignment);
      z-index: 1;
      transition: opacity 0.75s ease-in-out 0s;
    }

    #main>.inner {
      --padding-horizontal: 4rem;
      --padding-vertical: 4rem;
      --spacing: 2rem;
      --width: 64rem;
      border-radius: var(--border-radius-tl) var(--border-radius-tr) var(--border-radius-br) var(--border-radius-bl);
      max-width: 100%;
      position: relative;
      width: var(--width);
      z-index: 1;
      padding: var(--padding-vertical) var(--padding-horizontal);
    }

    #main>.inner>header {
      margin-bottom: var(--spacing);
    }

    #main>.inner>footer {
      margin-top: var(--spacing);
    }

    #main>.inner>*>* {
      margin-top: var(--spacing);
      margin-bottom: var(--spacing);
    }

    #main>.inner>*> :first-child {
      margin-top: 0 !important;
    }

    #main>.inner>*> :last-child {
      margin-bottom: 0 !important;
    }

    #main>.inner>.active {
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;
    }

    body.is-loading #main {
      opacity: 0;
    }

    body #wrapper:after {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      content: '';
      display: block;
      height: 3rem;
      left: calc(50% - 1.5rem);
      opacity: 0;
      pointer-events: none;
      position: fixed;
      top: calc(50% - 1.5rem);
      transition: opacity 1s ease, visibility 1s;
      visibility: hidden;
      width: 3rem;
      z-index: 100000;
    }

    body.is-loading {
      pointer-events: none;
    }

    body.is-loading.with-loader #wrapper:after {
      background-image: url('data:image/svg+xml;base64,');
      opacity: 1;
      transform: scale(1.0);
      visibility: visible;
    }

    body.is-playing.with-loader #wrapper:after {
      opacity: 0;
      transform: scale(0.5);
      transition: opacity 0.25s ease, transform 0.75s ease, visibility 0.25s;
      visibility: hidden;
    }

    #main>.inner>header,
    #main>.inner>footer {
      transition: opacity 0.125s ease-in-out 0.1875s, visibility 0.125s linear 0.1875s;
    }

    #main>.inner>header.hidden,
    #main>.inner>footer.hidden {
      transition: opacity 0.125s ease-in-out, visibility 0.125s;
      opacity: 0;
      visibility: hidden;
    }

    #main>.inner>section {
      transition: opacity 0.25s ease-in-out 0.125s, min-height 0.125s ease-in-out, max-height 0.125s ease-in-out;
    }

    #main>.inner>section.inactive {
      opacity: 0;
      transition: opacity 0.125s ease-in-out;
    }

    .inactive {
      display: none;
    }

    .active {
      display: block;
    }

    body.is-instant #main,
    body.is-instant #main>.inner>*,
    body.is-instant #main>.inner>section>* {
      transition: none !important;
    }

    body.is-instant:after {
      display: none !important;
      transition: none !important;
    }

