    }

    .icons li {
      position: relative;
      z-index: 1;
    }

    .icons li a {
      align-items: center;
      display: flex;
      justify-content: center;
    }

    .icons li a svg {
      display: block;
      position: relative;
    }

    .icons li a+svg {
      display: block;
      height: 100%;
      left: 0;
      pointer-events: none;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: -1;
    }

